@font-face {
  font-family: 'Gotham Pro';
  src: url('/catalog/view/theme/default/fonts/gotham-pro-regular.woff2') format('woff2');
  font-style: normal;
  font-weight: normal;
  font-display: swap;
}

@font-face {
  font-family: 'Gotham Pro';
  src: url('/catalog/view/theme/default/fonts/gotham-pro-bold.woff2') format('woff2');
  font-style: normal;
  font-weight: bold;
  font-display: swap;
}


ul {
  padding-left: 0;
  margin: 0;
  list-style-type: none;
}

:root {
  --container: 1630px;
  --containerGutter: 30px;
}

html, body {
  margin: 0;
}

body {
  font-family: "Gotham Pro",sans-serif;
  color: #303031;
  background-color: #F6F7F9;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
  flex-direction: column;
  min-width: 360px;
  min-height: 100%;
}


.body {
  font-size: 12px;
  line-height: 1.5;
}


@media (min-width: 1080px) {
.body {
    font-size: 14px;
}
}


.body .hide {
  display: none !important;
}


body .container {
  width: 100%;
  max-width: var(--container);
  margin-right: auto;
  margin-left: auto;
  padding-right: calc(var(--containerGutter) / 2);
  padding-left: calc(var(--containerGutter) / 2);
}


a {
  text-decoration: underline;
  text-decoration-skip-ink: none;
  outline: none;
  color: #BC191D;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}


button, input {
  outline: none;
}



