.menu {
  font-size: 12px;
  line-height: 21px;
  /* z-index: 10; */
}

@media (min-width: 1080px) {
  .menu {
    width: 307px;
  }
}

.menu__list {
  background: #fff;
}

.menu__list:not(.menu__list--main) {
  height: 100%;
  overflow: scroll;
  overflow-x: hidden;
  scrollbar-width: 0;
}

@media (min-width: 1080px) {
  .menu__list:not(.menu__list--main) {
    background-color: #fff;
    position: fixed;
    top: 0;
    left: 100%;
    width: 307px;
    display: none;
    border-left: 1px solid #edeff2;
    font-size: 14px;
  }
}

@media (min-width: 1080px) {
  .menu__item--active > .menu__list {
    display: block;
  }
}

.mobile-menu .menu__list--main {
  border-radius: 10px 0 0 10px;
  padding: 0 15px;
}

.mobile-menu .menu__list:not(.menu__list--main) {
  padding: 0 16px;
}

.menu__item {
  min-height: 44px;
  display: flex;
  align-items: center;
  /* transition: all .15s; */
  position: relative;
  padding: 0 15px;
}

.menu__item:nth-last-child(n+2) {
  border-bottom: 1px solid #edeff2;
}

.mobile-menu .menu__item {
  padding: 0;
}

.menu__item-img {
  width: 24px;
  height: 24px;
  margin-right: 8px;
  color: #b2bac7;
  stroke: currentColor;
  transition: color .3s ease;
}

.menu__item--active > .menu__link .menu__item-img,
.menu__item:hover > .menu__link .menu__item-img {
  color: #bc191d;
}


.menu__link {
  color: #303031;
  text-decoration: none;
  display: flex;
  align-items: center;
  padding: 4px 0;
  flex-grow: 1;
  /* z-index: 1; */
  transition: color .3s ease;
}

.menu__item--active > .menu__link,
.menu__item:hover > .menu__link {
  color: #bc191d;
}

.menu__link--main {
  font-weight: bold;
}

.menu__item-btn-arrow {
  display: flex;
  align-items: center;
  justify-content: center;
  /* margin: 0 -8px 0 auto; */
  flex-shrink: 0;
  width: 26px;
  border: none;
  background-color: transparent;
  color: #b2bac7;
  transition: color .3s ease;
  /* overflow: hidden; */
}

.menu__item--active > .menu__item-btn-arrow,
.menu__item:hover > .menu__item-btn-arrow {
  color: #bc191d;
}

.menu__item-btn-arrow-img {
  width: 6px;
  height: 10px;
  fill: currentColor;
}






.nav__item ul::-webkit-scrollbar {
  width: 0;
}
